import { expect } from 'chai';
import loader from '..';

describe('Test translation bundle loader', () => {
  it('loads root bundle', async function () {
    const bundle = await loader(null);
    expect(bundle).not.null;
    expect(Object.keys(bundle).length).greaterThan(0);
  });

  it('loads non-root bundle', async function () {
    const bundle = await loader('en');
    expect(bundle).not.null;
    expect(Object.keys(bundle).length).greaterThan(0);
  });
});
